<template>
  <div id="app">
    <div class="back" v-show='isShow'>
      <!-- <dianchangxiugai></dianchangxiugai> -->
      <div class="card1">
          <el-form ref="list" :model="list" label-width="80px">
            <el-form-item label="电厂名称">
              <el-input v-model="list.name1"></el-input>
            </el-form-item>
            <el-form-item label="电厂类型">
              <el-select v-model="form.region1" placeholder="请选择活动区域">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item lable-width="120px" label="发电机组容量" width='120'>
              <el-select v-model="form.region2" placeholder="请选择活动区域">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="升压站电压等级">
              <el-select v-model="form.region3" placeholder="请选择活动区域">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="所在省份">
              <el-select v-model="form.region4" placeholder="请选择活动区域">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="电厂地址">
              <el-input v-model="form.name2"></el-input>
            </el-form-item>
            <el-form-item label="电话">
              <el-input v-model="form.name3"></el-input>
            </el-form-item>
            <el-form-item label="邮编">
              <el-input v-model="form.name4"></el-input>
            </el-form-item>
            <el-button type="primary"  @click='Show'>修改   </el-button>
          </el-form>
        </div>
    </div>
    <div class="card2">
       <!-- <h2>{{ $route.query }}</h2> -->
      <div class="card2_top">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="电厂名称" >
            <el-input v-model="formInline.user" placeholder="电厂名称" id="input1"></el-input>
          </el-form-item>
          <el-form-item label="电厂类型">
            <el-select v-model="formInline.region" placeholder="电厂类型" id="input2">
              <el-option label="水电厂" value='t1'></el-option>
              <el-option label="火电厂" value='t2'></el-option>
              <el-option label="风电厂" value='t3'></el-option>
              <el-option label="雷电厂" value='t4'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="机组容量">
            <el-select v-model="formInline.region2" placeholder="活动区域" id="input3"> 
              <el-option label="110KV" value='s1'></el-option>
              <el-option label="120KV" value='s2'></el-option>
              <el-option label="90KV" value='s3'></el-option>
              <el-option label="20KV" value='s4'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="电压等级">
            <el-select v-model="formInline.region3" placeholder="活动区域" id="input4">
              <el-option label="1" value='l1'></el-option>
              <el-option label="2" value='l2'></el-option>
              <el-option label="3" value='l3'></el-option>
              <el-option label="3" value='l4'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click='search()'>查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="info" @click='reback'>重置</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="success">+新增</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div id="card2_bottom">
        <el-table :data="list.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%"
          @selection-change="handleSelectionChange">
          <el-table-column prop="name1" label="电厂名称">
          </el-table-column>
          <el-table-column prop="value0" label="电厂类型">
          </el-table-column>
          <el-table-column prop="value1" label="发电组容量">
          </el-table-column>
          <el-table-column prop="value2" label="升压站电压等级">
          </el-table-column>
          <el-table-column prop="name2" label="地址">
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row)" type="text" size="small">查看
              </el-button>
              <el-button type="text" size="small" @click='Show'>修改
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next, sizes, total, jumper" :page-sizes="[5, 10, 15, 20]"
          :page-size="pagesize" :total="list.length" @current-change="handleCurrentChange"
          @size-change="handleSizeChange">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import dianchangxiugai from './dianchangxiugai.vue'
  export default {
    name: 'xinjian',
    data() {
      return {
        isShow:false,
        form: {
          name1: this.name1,
          name2: '',
          name3:'',
          name4:'',
          value0: '',
          value1: '',
          value2: '',
          value3: '',

        },
        formInline: {
          user: '',
          region: ''
        },
        msg: 8888,
        list2:[

        ],
        list:[
        {
          name1:'西安火电厂',
          name2:'长安区',
          value0:'火电厂',
          value1:'10L',
          value2:'一级'

        },
        {
          name1:'长安火电厂',
          name2:'长安区',
          value0:'火电厂',
          value1:'10L',
          value2:'一级'

        },
        {
          name1:'百济火电厂',
          name2:'八大区',
          value0:'水电厂',
          value1:'20L',
          value2:'二级'

        },
        ],
        pagesize: 5,
        currpage: 1
      }
    },
    components:{
      dianchangxiugai
    },
    computed:{

    },

    created(){
      var str = localStorage.getItem('data')
        this.list.push(JSON.parse(str));
        this.list2 = this.list;
    },

    methods: {
      reback(){
        document.getElementById('input1').value='';
        this.list = this.list2;
      },
      Show(){
        this.isShow = !this.isShow;
      },
      handleCurrentChange(cpage) {
        this.currpage = cpage;
      },
      handleSizeChange(psize) {
        this.pagesize = psize;
      },
      handleSelectionChange(val) {
        console.log(val)
      },
      search() {
        var new_value = document.getElementById('input1').value;
        alert(new_value)
        // new_value = '';
        var newList = this.list.filter(item => {
          if (item.name1.includes(new_value)) {
            return item
          }
        })
        //查询方法,把搜索得关键字通过传参的方法传递给这个方法
        
        this.list = newList;
        console.log(newList)
      }
    },
    // mounted() {
      

    //     axios.get('http://192.168.1.139:8084/power/findall')
    //   //http://106.54.54.237:8000/api/v1/home/data?type=sell&page=1
    //       //then获取成功；response成功后的返回值（对象）

    //       .then(response => {

    //         console.log(response);
    //         alert(response);
    //         this.list = response.data.data;
    //       })

    //       //获取失败

    //       .catch(error => {

    //         console.log(error);

    //         alert('网络错误，不能访问');

    //       })

     
    // }
  }
  // alert(this.isShow)
</script>

<style lang="" scoped>
  /* #app{
    width: 100%;
    height: 100%;
    position: relative;
  } */
  .back{
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
    background-color:rgba(0,0,0,0.5);
    z-index: 2;
  }
  .el-input {
    width: 150px;
    height: 25px;
  }

  .el-select {
    width: 150px;
    height: 25px;
  }

  .el-form-item {
    margin-bottom: 10px;
  }

  .el-button {
    padding: 4px 8px;
  }


  .card2 {
    text-align: center;
    width: 95%;
    margin: auto;
    background: white;
  }

  .card1 {
    width: 550px;
    padding: 10px;
    margin: 0 auto;
    text-align: center;
    background: white;
  }
  .el-form-item__label{
    width: 100px !important;
    padding: 0 !important;
  }
  .card1 .el-input {
    width: 400px;
    height: 30px;
  }

  .card1 .el-select {
    width: 400px;
    height: 25px;
  }


  .card1 .el-button {
    padding: 8px 16px;
  }

  .element.style{
    width: 120px;
  }


</style>



